      memberImage: '/images/m001.jpg',
      memberName: '李行',
      memberDesignation: '導演',
      memberInfos: '<p>李行，本名李子達，1930年5月20日生於上海市，祖籍江蘇武進。年輕時即對戲劇極為著迷，在就讀蘇州國立社會教育學院藝術教育系戲劇組時，即參與過曹禺名劇《雷雨》的排練。1948年來台，進入省立師範學院（現國立師範大學）就讀，加入話劇社，參加演出及導演，並時常參與校外職業劇團與電影公司的演出。1952年6月畢業後入伍，接受第一期預備軍官訓練，結訓之後在省立師範學院附屬中學教了一年書。1954年，任自立晚報文教影劇記者，期間仍積極參與電影的演出。1958年，與張方霞、田豐聯合執導台聯影業公司的台語片《王哥柳哥遊台灣》，為其第一次執導演筒，此片轟動一時，帶動了台語片的喜劇風潮。1958年至1962年之間，李行拍攝了多部台語片，直至1962年的國語片《街頭巷尾》，結束了李行的台語片時期，開始了他導演生涯的第一個高峰。由於《街》片成績出色，李行應聘為中央電影公司的導演，並開啟了中影公司之後長達十五年的健康寫實風潮。</p><p>1963年追隨李嘉聯合執導的《蚵女》及1964年獨自執導的《養鴨人家》，都成為健康寫實的經典代表作。1964年的《婉君表妹》、1965年的《啞女情深》，帶動了六O年代的第一波瓊瑤小說改編電影的風潮，王戎、柯俊雄從此崛起影壇。1971年的《秋決》為其構思多年的影片，劇中探討的中國傳統文化、倫理觀、生命觀、時序倫常的哲理內涵及電影美學、手法等，引起社會、文藝界的廣泛討論，曾多次重映，其發人深省的倫理主題，使得本片成為最具代表的作品之一。1973年的《彩雲飛》、《心有千千結》、《海鷗飛處》則又帶動了第二波的瓊瑤小說改編電影風潮，甄珍、鄧光榮也因為《彩雲飛》而成為風靡一時的青春偶像。瓊瑤愛情電影風潮之後，李行再度回歸鄉土寫實路線，接連執導的《汪洋中的一條船》、《小城故事》、《早安台北》三部影片，連續獲得1978、1979、1980年的金馬獎最佳影片獎，創下台灣電影史上至今無人能破的紀錄，達到李行導演生涯的顛峰。綜觀李行導演四十多年導演生涯，走過的足跡都在台灣電影史上留下歷史意義，不論是健康寫實電影、三廳式瓊瑤愛情電影、或是台灣史詩式的電影，他的電影都能居於領導潮流的地位，不同時期都有其影響主流電影的經典佳作，因此李行在台灣電影史上的地位和影響，有著特殊的文化與藝術價值。</p><p>1990年行政院新聞局將金馬獎活動交由民間主辦，李行負責成立了「台北金馬影展執行委員會」常設機構，主辦第27、28、29、30、36、37共六屆金馬影展頒獎工作。期間並不斷的從事促進兩岸電影文化交流。1990年起，李行開啟了台灣電影界第一次率團訪問大陸的足跡，1992年又與香港導演協會會長吳思遠共同推動「海峽兩岸暨香港電影導演研討會」的舉辦，並首次邀請大陸影人訪台；1993年向新聞局爭取通過首屆「大陸電影觀摩展」，展演《香魂女》、《雙旗鎮刀客》、《秋菊打官司》等八部影片，在台北、台中、高雄放映。同年六月，「台灣電影觀摩展」在北京、成都、南京、上海巡迴放映。1999年「兩岸電影半世紀－－謝晉、李行影展」更促使上海、台北電影資料館永久典藏兩位導演的作品。李行對於促進兩岸交流活動至今仍不遺餘力，繼續參與電影活動盛事，並以電影終身義工自許，推動台灣新導演作品赴大陸上映，開拓大陸電影市場，為活絡台灣電影奉獻心力。</p><p>2009年，中華民國電影事業發展基金會成立兩岸電影交流委員會，李行擔任負責人，積極深耕廣植台灣電影在大陸的市場，每年的五月、六月分別在台北、大陸舉辦兩岸影展，推動台灣電影到大陸，邀請大陸電影到台灣交流，迄今（2015年）已舉辦七屆，藉由兩岸影展，鼓勵台灣新導演開發大陸市場。</p>',
